}
@media (max-width: 1199px) {
  .advantages__item-main {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin-left: 118px;
    margin-right: 118px;
  }
}
@media (max-width: 767px) {
  .advantages__item-main {
    grid-row: 3/4;
    margin: 0;
    max-height: 290px;
    box-sizing: border-box;
  }
}
.advantages__item-main .advantages__item-data-wrapper {
  gap: 25px;
}
.advantages__item-main p {
  margin: 0;
  font-size: 16px;
}
.advantages__item-main dd {
  font-size: 74px;
  margin: 0;
  color: #ffffff;
  background-image: none;
}
.advantages__item-main dt {
  color: #ffffff;
  font-size: 26px;
  font-weight: 700;
}
@media (max-width: 1199px) {
  .advantages__item-main dt {
    font-size: 22px;
  }
}

